* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

/*style for the basic bar*/
.bar{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.bar__text{
  -ms-flex-preferred-size: 90%;
      flex-basis: 90%;
  padding: 1rem;
  cursor: pointer;
  font-size: 1rem;
}

.bar__symbol{
  -ms-flex-preferred-size: 10%;
      flex-basis: 10%;
  padding: 1rem;
  font-size: 1.2rem;
  text-align: center;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease;
  cursor: pointer;
}




